body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
}

ol, ul {
    padding-left: 30px;
}

.board-row:after {
    clear: both;
    content: "";
    display: table;
}

.status {
    margin-bottom: 10px;
}

.square {
    background: #fff;
    border: 2px solid #999;
    float: left;
    font-size: 200px;
    font-weight: normal;
    line-height: 200px;
    height: 200px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 200px;
    color: #555;
}

.square:focus {
    outline: none;
}

.kbd-navigation .square:focus {
    background: #ddd;
}

.game {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.game-info {
    margin-left: 40px;
}
.game-info-announcement{
    font-size: 36px;
}
.game-mode {
    border: none;
    background-color: rgba(0, 0, 0, 0.1);
}
.game-mode:focus {
    outline: none;
}
.button-active{
    background-color: red;
    color: white;
}

@media only screen and (max-width: 1000px) {
    .square {
        background: #fff;
        border: 1px solid #999;
        float: left;
        font-size: 32px;
        font-weight: bold;
        line-height: 40px;
        height: 40px;
        margin-right: -1px;
        margin-top: -1px;
        padding: 0;
        text-align: center;
        width: 40px;
        color: #555;
    }
    .game-info-announcement{
        font-size: 20px;
    };
    .game-board{
        width: 100vw;
    }

    .game {
        flex-direction: column;
        align-items: center;
    }
    .game-info {
        margin-left: 0px;
        margin-top: 20px;
    }
}